@import './src/sass/daiclover.scss';

.Sobre_Mi {
  div.education {
    h3 {
      margin-bottom: .75rem;
    }

    h4.study {
      font-family: 'Kalam', cursive !important;
      font-size: 1rem;
      text-align: left;

      a {
        font-family: 'Kalam', cursive !important;
        text-decoration: none;
        color: #212529;

        &:hover {
          color: #007bff;
        }
      }
    }

    p.prom {
      font-family: 'Courgette', cursive !important;
      margin-top: .25rem;
      margin-bottom: 1rem;
      margin-left: .6rem;
      display: block;
      font-size: 80%;
      color: #6c757d;

      &.prom:before {
        content: "\2014\00A0";
      }
    }

    .accordion {
      button {
        text-decoration: none;
        .fa-glasses {
          transform: rotate(10deg);
        }
      }
    }
  }

  div.skills {
    .cardWrapper {
      .card {
        height: 300px;
        perspective: 1500px;
        background-color: transparent;
        border: none;

        .more {
          display: none;

          &:checked~.content {
            transform: rotateY(180deg);
          }
        }

        .content {
          position: relative;
          width: 100%;
          height: 100%;
          transform-style: preserve-3d;
          transition: transform 0.8s cubic-bezier(0.75, 0, 0.85, 1);

          .front,
          .back {
            background-color: white;
            background-image: url("../../../images/backgrounds/Scattered-Forcefields.svg"); /* background by SVGBackgrounds.com */
            position: absolute;
            width: 100%;
            -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
            transform-style: preserve-3d;
            border-radius: 6px;
            border: 1px solid rgba(0, 0, 0, 0.125);

            .card-body {
              transform: translateZ(80px) scale(0.94);
              width: 100%;
              display: flex;
              align-items: center;

              hr {
                width: 100%;
                margin-top: .15rem;
                margin-bottom: .3rem;
              }

              h5.card-subtitle,
              .prom {
                text-align: right;
              }

              p {
                margin-bottom: .25rem;
              }

              p.prom {
                margin-top: .25rem;
                margin-bottom: 1rem;
                display: block;
                font-size: 80%;
                color: #6c757d;

                &.prom:before {
                  content: "\2014\00A0";
                }
              }

              ul {
                // text-align: left;

                li {
                  padding: .25rem .50rem;
                  background-color: transparent;
                  border: none;
                }
              }
            }

            .rotate-btn {
              background-color: #ec407a;
              z-index: 1;
              margin: 10px;
              cursor: pointer;
              border-radius: 50%;
              border: solid 2px #fff;
              -webkit-box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
              box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
              -webkit-transition: all 0.2s ease-in-out;
              transition: all 0.2s ease-in-out;
              width: 40px;
              height: 40px;

              &:hover {
                background-color: #e940ecd1;
                box-shadow: 0 0 50px rgba(0, 0, 0, 0.5);
                text-shadow: none;
              }

              &.return {
                text-shadow: none;

                &:hover {
                  background-color: #355cc9ad;
                  box-shadow: none;
                }
              }

              i {
                color: #fff;
                font-size: 1.25rem;
              }
            }
          }

          .front {
            &:after {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              display: block;
              border-radius: 6px;
            }
          }

          .back {
            transform: rotateY(180deg);
          }
        }
      }
    }
  }

  .redes {
    padding: .25rem;
    font-size: 20px;
    list-style: none;
    margin: auto;

    a.btn-social {
      margin: .25rem;
      font-size: 20px;

      .fa-facebook-f {
        color: $facebook;
      }

      .fa-linkedin-in {
        color: $linkedin;
      }

      .fa-github {
        color: $github;
      }

      &:hover {
        i.fab,
        .torreCO {
          font-size: 25px;
          margin-top: -10px;
        }
      }
    }
  }
}